import ProjectsView from "../view/projects.js";
import SectionHeaderView from "../view/section-header.js";
import ProjectListView from "../view/project-list.js";
import ProjectItemView from "../view/project-item.js";
import {render} from "../utils/render.js";
import {Section, Projects as ProjectsData} from "../data/data.js";
import {Anchore} from "../const.js";

const {PROJECTS} = Section;
const {ABOUT} = Anchore;

export default class Projects {
  constructor(container) {
    this._container = container;
    this._projects = new ProjectsView();
    this._header = new SectionHeaderView(PROJECTS.title, PROJECTS.subtitle, ABOUT);
    this._projectList = new ProjectListView();
  }

  init() {
    render(this._container, this._projects);
    render(this._projects, this._header);
    render(this._projects, this._projectList);

    ProjectsData.map((project) =>
      render(this._projectList, new ProjectItemView(project))
    );
  }

  setOnScrollView() {
    this._projects.getElement().style.opacity = (window.pageYOffset > '950' ? 1 : 0);
  }

}