import SkillsView from "../view/skills.js";
import SectionHeaderView from "../view/section-header.js";
import SkillListView from "../view/skill-list.js";
import {render} from "../utils/render.js";
import {Section} from "../data/data.js";
import {Anchore} from "../const.js";

const {SKILLS} = Section;
const {PROJECTS} = Anchore;

export default class Skills {
  constructor(container) {
    this._container = container;
    this._skills = new SkillsView();
    this._header = new SectionHeaderView(SKILLS.title, SKILLS.subtitle, PROJECTS);
    this._skillList = new SkillListView();
  }

  init() {
    render(this._container, this._skills);
    render(this._skills, this._header);
    render(this._skills, this._skillList);

  }

  setOnScrollView() {
    this._skills.getElement().style.opacity = (window.pageYOffset > '250' ? 1 : 0);
  }
}
