import GreetingView from "./view/greeting.js";
import GreetingPresenter from "./presenter/greeting.js";
import SkillsPresenter from "./presenter/skills.js";
import ProjectsPresenter from "./presenter/projects.js";
import AboutPresenter from "./presenter/about.js";
import {setBackground} from "./utils/background.js";
import {render} from "./utils/render.js";
import "./styles/main.scss";

const main = document.querySelector(`.main`);
const animatedTitle = document.querySelector(`.header__title`);
const footer = document.querySelector(`.footer`);

const greeting = new GreetingView();

setInterval(setBackground, 33)

render(main, greeting);

const messageStart = greeting.getElement().querySelector(`.message--start`);
const computer = greeting.getElement().querySelector(`.computer`);
const messageEnd = greeting.getElement().querySelector(`.message--end`);

const greetingPresenter = new GreetingPresenter(messageStart, computer, messageEnd);
const skillsPresenter = new SkillsPresenter(main);
const projectsPresenter = new ProjectsPresenter(main);
const aboutPresenter = new AboutPresenter(main);

animatedTitle.addEventListener('animationend', () => {
  const timer = setTimeout(() => {
    greetingPresenter.init();
    skillsPresenter.init();
    projectsPresenter.init();
    aboutPresenter.init();
    clearTimeout(timer);
  }, 1700);
});

window.onscroll = () => {
  skillsPresenter.setOnScrollView();
  projectsPresenter.setOnScrollView();
  aboutPresenter.setOnScrollView();
  footer.style.opacity = (window.pageYOffset > '4300' ? 1 : 0);
  console.log(window.pageYOffset)
}
