import AbstractView from "./abstract.js";

const createMessageMarkup = (message) => {
  return (
    `<p class="message__text">${message}</p>`
  );
};

export default class Message extends AbstractView {
  constructor(text) {
    super();
    this._text = text;
  }

  getTemplate() {
    return createMessageMarkup(this._text);
  }
}
