import {getRandomValue} from "../utils/utils.js";
import {Display} from "../const.js";

const Color = {
  LINE: `green`,
  NUMBER: `lime`,
  SQUARE: `#90ee90`,
};

const Number = {
  MIN: 0,
  MAX: 9
};

const SquareSize = {
  MIN: 4,
  MAX: 9,
};

export default class Animation {
  constructor() {
    this.number = null;
    this.square = null;
  }

  init(ctx) {
    this.number = Math.floor(getRandomValue(Number.MIN, Number.MAX));
    this.square = getRandomValue(SquareSize.MIN, SquareSize.MAX);
    this._renderLines(ctx);
    this._renderNumbers(ctx);
    this._renderSquares(ctx);
  }

  _renderLines(ctx) {
    const x = getRandomValue(-Display.WIDTH, Display.WIDTH);
    const y = Display.HEIGHT;

    ctx.strokeStyle = Color.LINE;
    ctx.beginPath();
    ctx.moveTo(x, 0);
    ctx.lineTo(x, y);
    ctx.closePath();
    ctx.stroke();
  }

  _renderNumbers(ctx) {
    const x = getRandomValue(-Display.WIDTH, Display.WIDTH);
    const y = getRandomValue(0, Display.HEIGHT);

    ctx.fillStyle = Color.NUMBER;
    ctx.font = '18px serif';
    ctx.textBaseline = 'hanging';
    ctx.fillText(this.number, x, y);
  }

  _renderSquares(ctx) {
    const x = getRandomValue(-Display.WIDTH, Display.WIDTH);
    const y = getRandomValue(0, Display.HEIGHT);

    ctx.fillStyle = Color.SQUARE;
    ctx.fillRect(x, y, this.square, this.square);
  }
};
