import MessageView from "../view/message.js";
import ComputerView from "../view/computer.js";
import CodeView from "../view/code.js";
import LineView from "../view/line.js";
import LoaderView from "../view/loader.js";
import RepeatButtonView from "../view/repeat-button.js";
import SocialView from "../view/social.js";
import {render, replace, remove} from "../utils/render.js";
import {Message, Code} from "../data/data.js";

const {START, END} = Message;
const START_MESSAGES_COUNT = 3;
const CODE_LINES_COUNT = 13;
const END_MESSAGES_COUNT = 4;

export default class Greeting {
  constructor(messageStart, computer, messageEnd) {

    this._messageStartContainer = messageStart;
    this._computerContainer = computer;
    this._messageEndContainer = messageEnd;

    this._startMessages = new Array(START_MESSAGES_COUNT).fill(``).map((_item, i) => {
      return new MessageView(START[`part${i + 1}`]);
    });

    this._endMessages = new Array(END_MESSAGES_COUNT).fill(``).map((_item, i) => {
      return new MessageView(END[`part${i + 1}`]);
    });

    this._computer = new ComputerView();
    this._code = new CodeView();
    this._loader = new LoaderView();
    this._button = new RepeatButtonView();
    this._social = new SocialView();
    this._timer = null;

    this._codeLines = new Array(CODE_LINES_COUNT).fill(``).map((_item, i) => {
      return new LineView(Code[`line${i + 1}`]);
    });

    this._handleRepeatButtonClick = this._handleRepeatButtonClick.bind(this);
}

  init() {
    render(this._messageStartContainer, this._startMessages[0]);
    this._setStartMessagesAnimationHanldlers();
    this._setEndMessagesAnimationHanldlers();
    this._setComputerAnimationHandlers();
    this._setCodeAnimationHandlers();
    this._setLoaderAnimationHandler();
    this._button.setClickHandler(this._handleRepeatButtonClick);
  }

  destroy() {
    this._startMessages.forEach((it, i) => {
      remove(it);
    });
    this._endMessages.forEach((it, i) => {
      remove(it);
    });
    remove(this._computer);
    remove(this._code);
    remove(this._loader);
    remove(this._button);
    remove(this._social);
  }

  _handleRepeatButtonClick() {
    this.destroy();
    this.init();
  }

  _setStartMessagesAnimationHanldlers() {

    this._startMessages.forEach((it, i) => {
        const message = it.getElement();

      if (i < START_MESSAGES_COUNT - 1) {
       message.addEventListener('animationend', () => {
      render(this._messageStartContainer, this._startMessages[i + 1]);
      });
        } else {
           message.addEventListener('animationend', () => {
            render(this._computerContainer, this._computer);
      });
        }
    });
  }

  _setEndMessagesAnimationHanldlers() {
    this._endMessages.forEach((it, i) => {
      const message = it.getElement();

    if (i < END_MESSAGES_COUNT - 1) {
     message.addEventListener('animationend', () => {
    render(this._messageEndContainer, this._endMessages[i + 1]);
    });
      } else {
        message.addEventListener('animationend', () => {
         render(this._messageEndContainer, this._button);
         render(this._messageStartContainer, this._social);
         clearTimeout(this._timer);
   });
     }
  });
  }

  _setComputerAnimationHandlers() {
    this._computer.getElement().addEventListener('animationend', () => {
      render(this._computerContainer, this._code);
      render(this._code, this._codeLines[0]);
    });
  }

  _setCodeAnimationHandlers() {
    this._codeLines.forEach((it, i) => {
        const line = it.getElement();

      if (i < CODE_LINES_COUNT - 1) {
       line.addEventListener('animationend', () => {
      render(this._code, this._codeLines[i + 1]);
      });
        } else {
        line.addEventListener('animationend', () => {
          const timer = setTimeout(() => {
            replace(this._code, this._loader);
            clearTimeout(timer);
          }, 1000)
      });
      };
    });
  }

  _setLoaderAnimationHandler() {
    this._loader.getElement().addEventListener('animationend', () => {
      remove(this._loader);
      this._computer.setupAnimation();
      this._timer = setTimeout(() => render(this._messageEndContainer, this._endMessages[0]), 2000);
    });
  }
};
